*, *::before, *::after {
  box-sizing: border-box;
}
html {
  margin: 0;
  padding: 0;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Nunito Sans',sans-serif;
  background-color: #e6e7ee; 
  overflow-x: hidden;
}
nav {
  width: 100%;
  display: flex;
  padding-top: 0.5rem;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.nav ul {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  padding-top: 0.4rem;
  margin: 0 !important;
}

.nav-link {
  display: inline;
  padding: 0.45rem 1.2rem;
  text-decoration: none;
  font-weight: 300;
  margin-left:2rem !important;
  cursor: pointer;
  letter-spacing: 0.025em;
  border-radius: 0.55rem;
  color: rgb(92, 91, 91);
  font-weight: 500;
  border: 0.0625rem solid transparent;
  box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #ffffff;
  border: 0.0625rem solid #D1D9E6;
}

.nav-link:hover, .nav-link:focus {
  text-decoration: none;
  box-shadow: inset 2px 2px 5px #b8b9be,
              inset -3px -3px 7px #ffffff;
  transition: all 0.2s ease;
  border: 0.0625rem solid #D1D9E6;
}
.social-icons ul {
  display: flex ;
}
.social-icons a {
  border-radius: 2rem;
  color: #31344b;
  background-color: #e6e7ee;
  position: relative;
  margin: 0 1rem 1rem 0;
  font-size: 1.5rem;
  border: 0.0625rem solid #D1D9E6;
  box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #ffffff;
  width: 3.575rem;
  padding: 0.5rem;
  letter-spacing: 0.025em;
  height: 3.575rem;
}
.social-icons a:hover,
.social-icons a:focus {
  box-shadow:inset 3px 3px 6px #b8b9be,inset -3px -3px 6px #ffffff;
}
.fab  {
  color: rgb(92, 91, 91) !important;
  text-align: center;
  margin-left: 2px;
}

.wrapper {
  max-width: 1200px;
  height: 91vh;
  color: rgb(92, 91, 91);
  font-weight: 500;
  margin: 0 auto;
  margin-top: 1.2rem;
  background-clip: padding-box;
  border: 0.0625rem solid #e2e6ee;
  border-radius: 0.55rem;
  box-shadow: 2px 2px 5px #b8b9be,  -3px -3px 7px #ffffff
}
.main {
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff;
  max-height: 90vh;
  height: 88.3vh;
  margin: 0.5rem;
  border: 0.0625rem solid #D1D9E6;
  padding: 0.7rem;
  border-radius: 0.55rem;
}
.inside-wrapper {
  box-shadow:  2px 2px 5px #b8b9be,  -3px -3px 7px #ffffff;
  max-height: 85vh;
  height: 85.8vh;
  padding: 0.5rem;
 
  border-radius: 0.55rem;
}
.grid {
  display: grid;
  height: 50%;
  column-gap: 0.8rem;
  margin-left: 0.2rem;
  grid-template-columns: repeat(4,1fr);
  text-align: center;

}
.grid2 {
  display: grid;
  height: 50%;
  border-radius: 0.55rem;
  grid-template-columns: repeat(1,1fr);
  text-align: center;

}
h3 {
  font-size: 0.875rem;
  padding: 1rem 1.5rem;
  border-radius: 0.55rem;
  border: 0.0625rem solid #D1D9E6;
  border-radius: 0.55rem;
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #ffffff !important;

}
@media (max-width:600px) {
  .logo {
    position: absolute;
    left: 83%;
    top: 6%;
  }
  .nav {
    height: 120px;
  }
  a.nav-link {
    padding: 0.35rem 0.5rem;
    margin-left:0.5rem !important;
  }
  .social-icons a {
    margin: 0 0.5rem 0.5rem 0;
  font-size: 1rem;
  }
  .grid {
    grid-template-columns: repeat(2,1fr);
  }
  .grid2 {
    grid-template-columns: repeat(1,1fr);
  }
}